
html,
body {
  padding: 0;
  margin: 0;
  width: 100%;
  height: 100%;
}
body {
  background: url("assets/img/bg.png") no-repeat 0 0;
  background-size: 100% 100%;
  overflow: hidden;
}
/** layout */
.middle {
  position: absolute;
  left: 0;
  right: 0;
  margin: 0 auto;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}
/** position */
.absolute {
  position: absolute;
}
.relative {
  position: relative;
}
/** border */
/** icon */
.i-icon {
  display: inline-block;
  background-position: center center;
  background-size: 100% 100%;
}
.i-icon-user {
  background: url("assets/img/user.png") no-repeat;
  width: 23px;
  height: 23px;
}
.i-icon-lock {
  width: 22px;
  height: 26px;
  background: url("assets/img/lock.png") no-repeat;
}
/** input */
.i-input {
  background: none;
  border: none;
  outline: none;
  display: inline-block;
  -webkit-tap-highlight-color: transparent;
  border-radius: 0;
  -webkit-appearance: none;
}
.i-input__sample {
  border-bottom: 1px solid #00f3f3;
}
.i-banner {
  position: absolute;
  left: 0;
  right: 0;
  margin: 0 auto;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  width: 1030px;
  height: 1030px;
  background: url("assets/img/banner.png") center center no-repeat;
  background-size: 1030px 870px;
}
.i-banner.animated {
  opacity: 0;
  -webkit-animation: fadeIn 1s linear 1s forwards;
          animation: fadeIn 1s linear 1s forwards;
}
.i-main {
  position: absolute;
  left: 0;
  right: 0;
  margin: 0 auto;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  margin-left: -20px;
  margin-top: 10px;
  z-index: 100;
}
.i-main .i-circle {
  position: absolute;
  left: 0;
  right: 0;
  margin: 0 auto;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  background: url("assets/img/waiquan.png") center center no-repeat;
  width: 511px;
  height: 511px;
  background-size: 511px 508px;
}
.i-main .i-circle.animated {
  opacity: 0;
  -webkit-transform: translateY(100%) scale(2);
          transform: translateY(100%) scale(2);
  -webkit-animation: fadeScaleIn 1s ease-in-out forwards, rotate360 45s linear 2s infinite;
          animation: fadeScaleIn 1s ease-in-out forwards, rotate360 45s linear 2s infinite;
}
.i-main .i-circle.paused {
  -webkit-animation-play-state: paused;
          animation-play-state: paused;
}
.i-main .i-blue__circle {
  position: absolute;
  left: 0;
  right: 0;
  margin: 0 auto;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  background: url("assets/img/blue_circle.png") center 22px no-repeat;
  width: 363px;
  height: 369px;
  background-size: 363px 347px;
}
.i-main .i-blue__circle.animated {
  opacity: 0;
  -webkit-transform: translateY(100%) scale(2);
          transform: translateY(100%) scale(2);
  -webkit-animation: fadeScaleIn 1s ease-in-out 500ms forwards, rotate360R 20s linear 2s infinite;
          animation: fadeScaleIn 1s ease-in-out 500ms forwards, rotate360R 20s linear 2s infinite;
}
.i-main .i-black__circle360 {
  position: absolute;
  left: 0;
  right: 0;
  margin: 0 auto;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  background: url("assets/img/black_circle360.png") center center no-repeat;
  width: 328px;
  height: 328px;
  margin-top: 4px;
  background-size: 328px 328px;
}
.i-main .i-black__circle360.animated {
  opacity: 0;
  -webkit-transform: translateY(100%) scale(2);
          transform: translateY(100%) scale(2);
  -webkit-animation: fadeScaleIn 1s ease-in-out 700ms forwards;
          animation: fadeScaleIn 1s ease-in-out 700ms forwards;
}
.i-main .i-white__circle {
  position: absolute;
  left: 0;
  right: 0;
  margin: 0 auto;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  background: url("assets/img/white_circle.png") left center no-repeat;
  width: 330px;
  height: 330px;
  margin-top: 4px;
  background-size: 315px 330px;
}
.i-main .i-white__circle.animated {
  opacity: 0;
  -webkit-transform: translateY(100%) scale(2);
          transform: translateY(100%) scale(2);
  -webkit-animation: fadeScaleIn 1s ease-in-out 800ms forwards, rotate360 15s linear 2s infinite;
          animation: fadeScaleIn 1s ease-in-out 800ms forwards, rotate360 15s linear 2s infinite;
}
.i-main .i-lightblue__circle {
  position: absolute;
  left: 0;
  right: 0;
  margin: 0 auto;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  background: url("assets/img/lightblue_circle.png") center bottom no-repeat;
  width: 300px;
  height: 300px;
  background-size: 266px 86px;
}
.i-main .i-lightblue__circle.animated {
  opacity: 0;
  -webkit-transform: translateY(100%) scale(2);
          transform: translateY(100%) scale(2);
  -webkit-animation: fadeScaleIn 1s ease-in-out 800ms forwards, rotate360R 10s linear 2s infinite;
          animation: fadeScaleIn 1s ease-in-out 800ms forwards, rotate360R 10s linear 2s infinite;
}
.i-main .i-gray__circle {
  position: absolute;
  left: 0;
  right: 0;
  margin: 0 auto;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  background: url("assets/img/gray_circle.png") center center no-repeat;
  width: 246px;
  height: 246px;
  margin-top: 3px;
  background-size: 100% 100%;
}
.i-main .i-gray__circle.animated {
  opacity: 0;
  -webkit-transform: translateY(100%) scale(2);
          transform: translateY(100%) scale(2);
  -webkit-animation: fadeScaleIn 1s ease-in-out 1s forwards;
          animation: fadeScaleIn 1s ease-in-out 1s forwards;
}
@-webkit-keyframes fadeIn {
0% {
    opacity: 0;
}
100% {
    opacity: 1;
}
}
@keyframes fadeIn {
0% {
    opacity: 0;
}
100% {
    opacity: 1;
}
}
@-webkit-keyframes rollIn {
0% {
    opacity: 0;
    -webkit-transform: scale(0);
            transform: scale(0);
}
100% {
    opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1);
}
}
@keyframes rollIn {
0% {
    opacity: 0;
    -webkit-transform: scale(0);
            transform: scale(0);
}
100% {
    opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1);
}
}
@-webkit-keyframes fadeScaleIn {
0% {
    opacity: 0;
    -webkit-transform: translateY(100%) scale(2);
            transform: translateY(100%) scale(2);
}
100% {
    opacity: 1;
    -webkit-transform: translateY(-50%) scale(1);
            transform: translateY(-50%) scale(1);
}
}
@keyframes fadeScaleIn {
0% {
    opacity: 0;
    -webkit-transform: translateY(100%) scale(2);
            transform: translateY(100%) scale(2);
}
100% {
    opacity: 1;
    -webkit-transform: translateY(-50%) scale(1);
            transform: translateY(-50%) scale(1);
}
}
@-webkit-keyframes fadeTranslateIn {
0% {
    opacity: 0;
    -webkit-transform: translateY(-100%);
            transform: translateY(-100%);
}
100% {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
}
}
@keyframes fadeTranslateIn {
0% {
    opacity: 0;
    -webkit-transform: translateY(-100%);
            transform: translateY(-100%);
}
100% {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
}
}
@keyframes fadeIn {
0% {
    opacity: 0;
}
100% {
    opacity: 1;
}
}
/** animation */
@-webkit-keyframes rotate360 {
0% {
    -webkit-transform: translateY(-50%) rotate(0);
            transform: translateY(-50%) rotate(0);
}
100% {
    -webkit-transform: translateY(-50%) rotate(360deg);
            transform: translateY(-50%) rotate(360deg);
}
}
@keyframes rotate360 {
0% {
    -webkit-transform: translateY(-50%) rotate(0);
            transform: translateY(-50%) rotate(0);
}
100% {
    -webkit-transform: translateY(-50%) rotate(360deg);
            transform: translateY(-50%) rotate(360deg);
}
}
@-webkit-keyframes rotate360R {
0% {
    -webkit-transform: translateY(-50%) rotate(360deg);
            transform: translateY(-50%) rotate(360deg);
}
100% {
    -webkit-transform: translateY(-50%) rotate(0);
            transform: translateY(-50%) rotate(0);
}
}
@keyframes rotate360R {
0% {
    -webkit-transform: translateY(-50%) rotate(360deg);
            transform: translateY(-50%) rotate(360deg);
}
100% {
    -webkit-transform: translateY(-50%) rotate(0);
            transform: translateY(-50%) rotate(0);
}
}
@-webkit-keyframes rotate45 {
from {
    -webkit-transform: translateY(-50%) rotate(0);
            transform: translateY(-50%) rotate(0);
}
to {
    -webkit-transform: translateY(-50%) rotate(45deg);
            transform: translateY(-50%) rotate(45deg);
}
}
@keyframes rotate45 {
from {
    -webkit-transform: translateY(-50%) rotate(0);
            transform: translateY(-50%) rotate(0);
}
to {
    -webkit-transform: translateY(-50%) rotate(45deg);
            transform: translateY(-50%) rotate(45deg);
}
}
.rotate45 {
  -webkit-animation-name: rotate45;
          animation-name: rotate45;
  -webkit-animation-timing-function: ease-out;
          animation-timing-function: ease-out;
  -webkit-animation-fill-mode: both;
          animation-fill-mode: both;
  -webkit-animation-duration: 500ms;
          animation-duration: 500ms;
  -webkit-animation-iteration-count: normal;
          animation-iteration-count: normal;
}
.rotate36 {
  -webkit-animation-name: rotate360;
          animation-name: rotate360;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
  -webkit-animation-fill-mode: both;
          animation-fill-mode: both;
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
}



