/** layout */
.middle {
  position: absolute;
  left: 0;
  right: 0;
  margin: 0 auto;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}
/** position */
.absolute {
  position: absolute;
}
.relative {
  position: relative;
}
/** border */
/** icon */
.i-icon {
  display: inline-block;
  background-position: center center;
  background-size: 100% 100%;
}
.i-icon-user {
  background: url("../assets/img/user.png") no-repeat;
  width: 23px;
  height: 23px;
}
.i-icon-lock {
  width: 22px;
  height: 26px;
  background: url("../assets/img/lock.png") no-repeat;
}
/** input */
.i-input {
  background: none;
  border: none;
  outline: none;
  display: inline-block;
  -webkit-tap-highlight-color: transparent;
  border-radius: 0;
  -webkit-appearance: none;
}
.i-input__sample {
  border-bottom: 1px solid #00f3f3;
}
#index {
  width: 100%;
  height: 100%;
  position: absolute;
}
#index canvas {
  position: absolute;
  left: 0;
  right: 0;
  margin: 0 auto;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  opacity: 0;
}
#index canvas.animated {
  -webkit-animation: fadeIn 1s ease-in-out 1s forwards;
          animation: fadeIn 1s ease-in-out 1s forwards;
}
.i-map {
  position: absolute;
  left: 0;
  right: 0;
  margin: 0 auto;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  width: 260px;
  height: 260px;
  z-index: 0;
}
.i-map > img {
  display: block;
  width: 100%;
  opacity: 0;
  -webkit-transform: scale(0);
          transform: scale(0);
}
.i-map > img.animated {
  -webkit-animation: rollIn 2s ease-in-out forwards;
          animation: rollIn 2s ease-in-out forwards;
}



