/** layout */
.middle {
  position: absolute;
  left: 0;
  right: 0;
  margin: 0 auto;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}
/** position */
.absolute {
  position: absolute;
}
.relative {
  position: relative;
}
/** border */
/** icon */
.i-icon {
  display: inline-block;
  background-position: center center;
  background-size: 100% 100%;
}
.i-icon-user {
  background: url("../assets/img/user.png") no-repeat;
  width: 23px;
  height: 23px;
}
.i-icon-lock {
  width: 22px;
  height: 26px;
  background: url("../assets/img/lock.png") no-repeat;
}
/** input */
.i-input {
  background: none;
  border: none;
  outline: none;
  display: inline-block;
  -webkit-tap-highlight-color: transparent;
  border-radius: 0;
  -webkit-appearance: none;
}
.i-input__sample {
  border-bottom: 1px solid #00f3f3;
}
audio.i-audio {
  position: absolute;
  width: 0;
  height: 0;
  display: none;
}
.i-form {
  position: absolute;
  left: 0;
  right: 0;
  margin: 0 auto;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  z-index: 101;
  width: 160px;
  height: 160px;
}
.i-form .i-form__item {
  position: relative;
  margin-top: 20px;
  opacity: 0;
  -webkit-transform: translateY(-100%);
          transform: translateY(-100%);
  -webkit-animation: fadeTranslateIn 1s ease-in-out 0.5s forwards;
          animation: fadeTranslateIn 1s ease-in-out 0.5s forwards;
}
.i-form .i-form__item:after {
  content: '';
  height: 1px;
  margin-top: -1px;
  position: absolute;
  left: 0;
  width: 0;
  bottom: 0;
  transition: all .5s;
  box-shadow: 0 0 2px #00f3f3, 0 0 5px #00f3f3, 0 0 10px #00f3f3;
}
.i-form .i-form__item:first-child {
  margin-top: 25px;
}
.i-form .i-form__item:nth-child(2) {
  -webkit-animation-delay: 1s;
          animation-delay: 1s;
}
.i-form .i-form__item.active:after {
  width: 162px;
}
.i-form .i-form__item.active > i {
  -webkit-transform: scale(0.65);
          transform: scale(0.65);
  top: -15px;
}
.i-form .i-form__item.active > span {
  width: 80px;
  margin-left: 20px;
}
.i-form .i-form__item.active .i-input {
  text-indent: 10px;
}
.i-form .i-form__item > i {
  position: absolute;
  top: 5px;
  -webkit-transform-origin: 0 0;
          transform-origin: 0 0;
  transition: all .5s;
}
.i-form .i-form__item > span {
  position: absolute;
  color: #00f3f3;
  margin-left: 30px;
  top: -15px;
  font-size: 12px;
  width: 0;
  overflow: hidden;
  transition: all .5s;
  white-space: nowrap;
}
.i-form .i-form__item > .i-input {
  line-height: 32px;
  height: 32px;
  width: 160px;
  color: #00f3f3;
  text-indent: 32px;
  font-size: 18px;
  transition: all .5s;
  position: relative;
}
.i-form .i-form__submit {
  text-align: center;
  margin-top: 20px;
  opacity: 0;
  -webkit-transform: translateY(-100%);
          transform: translateY(-100%);
  -webkit-animation: fadeTranslateIn 1s ease-in-out 1.5s forwards;
          animation: fadeTranslateIn 1s ease-in-out 1.5s forwards;
}
.i-form .i-form__submit > a {
  display: inline-block;
  cursor: pointer;
  position: relative;
}
.i-form .i-form__submit > a:before,
.i-form .i-form__submit > a:after {
  transition: all .5s;
  position: absolute;
  content: '';
  width: 18px;
  height: 19px;
  left: 8px;
  top: -1px;
  -webkit-transform: skewX(45deg);
          transform: skewX(45deg);
}
.i-form .i-form__submit > a:after {
  top: 20px;
  -webkit-transform: skewX(-45deg);
          transform: skewX(-45deg);
}
.i-form .i-form__submit > a:hover:before,
.i-form .i-form__submit > a:hover:after {
  box-shadow: 0 0 5px #00f3f3, 0 0 20px #00f3f3, 0 0 30px #00f3f3, 0 0 50px #00f3f3;
}



